import {Component, OnInit} from '@angular/core';
import * as wilddog from 'wilddog';
import {MdDialog, MdDialogRef} from '@angular/material';
import {DialogComponent} from './shared/components/dialog/dialog.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  color: string;
  wild_dog_ref: any;
  text: string;
  dialogRef: MdDialogRef<DialogComponent>
  availableColors = [
    {name: 'none', color: ''},
    {name: 'Primary', color: 'primary'},
    {name: 'Accent', color: 'accent'},
    {name: 'Warn', color: 'warn'}
  ];
  selectedValue: string;
  text_array: any[];
  foods = [
    {value: 'steak-0', viewValue: 'Steak'},
    {value: 'pizza-1', viewValue: 'Pizza'},
    {value: 'tacos-2', viewValue: 'Tacos'}
  ];

  constructor(public dialog: MdDialog) {
    this.text = '';
    this.text_array = [];
  }

  submit() {
    console.log('i am click');
    if (this.text === '') {
      this.dialogRef = this.dialog.open(DialogComponent);
      return;
    } else {
      this.wild_dog_ref.child('message').push(this.text);
      this.text = '';
    }
  }

  cancel() {
    this.text = '';
  }

  ngOnInit() {
    const config = {
      syncURL: 'https://xiaoliang2233.wilddogio.com',
      authDomain: 'xiaoliang2233.wilddog.com',
    };
    wilddog.initializeApp(config);
    this.wild_dog_ref = wilddog.sync().ref();
    this.wild_dog_ref.child('message').on('child_added', snapshot => {
      this.text_array.unshift(snapshot.val());
    });
  }

}




